<template lang="pug">
	#app
		.navigation
			.logo
				router-link(to="/")
					icon(name="code")
					//- icon(name='rebel')
			ul.menu_link
				li
					router-link(to='/') Home
				li
					router-link(to='/about') About
				li
					router-link(to='/contacts') Contacts
				li
					router-link(to='/works') Works
		nu-breadcrumbs
		router-view
		vue-progress-bar
</template>

<script>
	export default {
		name: 'app',
		data() {
			return {
				logo: ''
			}},
		mounted () {
			this.$Progress.finish()},
		created () {
			this.$Progress.start();
			this.$router.beforeEach((to, from, next) => {
				if (to.meta.progress !== undefined) {
					let meta = to.meta.progress;
					this.$Progress.parseMeta(meta)
				}
				this.$Progress.start();
				next();
			});
			this.$router.afterEach((to, from) => {
				this.$Progress.finish()
			})}
	}
</script>

<style lang='stylus' src='./assets/style/all.styl'></style>



