import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import VueProgressBar from 'vue-progressbar'
import 'vue-awesome/icons'
import Icon from 'vue-awesome/components/Icon'

import NuModal from 'nu-modal-vue'
import NuBreadcrumbs from 'nu-breadcrumbs'

// pages components
import home from './components/home'
import about from './components/about'
import contacts from './components/contacts'
import works from './components/works'
import health24 from './components/works/24health'

// firebase
import firebase from 'firebase'
import Auth from './components/login/Auth'
import AuthSuccess from './components/login/AuthSuccess'
import {config} from './components/login/firebaseConfig'


Vue.component('icon', Icon);

Vue.use(NuModal);
Vue.use(NuBreadcrumbs);

Vue.use(VueProgressBar, {
  color: '#42b983',
  failedColor: '#874b4b',
  thickness: '3px',
  transition: {
    speed: '0.5s',
    opacity: '0.2s',
    termination: 300
  }
});

const routes = [
  { path: '/', component: home},
  { path: '/about', component: about},
  { path: '/contacts', component: contacts},
  { path: '/works', component: works},
  { path: '/works/24health', component: health24}
];

Vue.use(VueRouter);
const router = new VueRouter({
  routes,
  mode: 'history'
});

new Vue({
  el: '#app',
  template: '<App/>',
  components: { App },
  router
}).$mount('#app');



